<template>
<body id="poster">
  <el-form class="login-container" label-position="left" label-width="0px">
    <h3 class="login_title">用户登陆</h3>
    <el-form-item>
      <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="Username"></el-input>
    </el-form-item>

    <el-form-item>
      <el-input
        type="password"
        v-model="loginForm.password"
        auto-complete="off"
        placeholder="Password"
      ></el-input>
    </el-form-item>

    <el-form-item prop="validate">
      <el-input v-model="loginForm.validate" class="validate-code" placeholder="Validcode"></el-input>
      <div class="code" @click="refreshCode">
        <s-identify :identifyCode="identifyCode"></s-identify>
      </div>
    </el-form-item>

    <!-- <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%;background: #505458;border: none" v-on:click="login">Login</el-button>
    </el-form-item>-->
    <el-button
      :type="primary"
      round
      style="font-size:18px;line-height:20px;width: 100%;color: #FFFFFF; background:#505458 ;border: none; "
      @click="login()"
    >Login</el-button>
    <h5></h5>
    <el-link
      :underline="false"
      type="info"
      icon="el-icon-user"
      style="font-size:18px;line-height:30px; float:right;"
    >Don't have a account? Create one now!</el-link>
  </el-form>
</body>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
      errorInfo: false,
      loginForm: {
        username: "",
        password: "",
        validate: ""
      },
      responseResult: []
    };
  },
  rules: {
    username: [{ required: true, message: "请输入登录名", trigger: "blur" }],
    password: [{ required: true, message: "请输入密码", trigger: "blur" }],
    validate: [{ required: true, message: "请输入验证码", trigger: "blur" }]
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  methods: {
    gotoRegister() {
      this.$router.push("/register");
    },
    login() {
      // const self = this;
      // self.$refs[formName].validate(valid => {
      //   if (valid) {
      //     if (self.loginForm.validate != this.identifyCode) {
      //       self.errorInfo = true;
      //       self.errInfo = "验证码错误";
      //       self.$message.error("验证码错误");
      //       this.refreshCode();
      //     } else {
      //       this.$axios
      //         .post("/login", {
      //           username: this.loginForm.username, //cdy
      //           password: this.loginForm.password //1234
      //         })
      //         .then(res => {
      //           if (res.data === 1) {
      //             // console.log(res.dat
      //             this.$message({
      //               message: "登录成功，正在跳转。。。。",
      //               type: "success"
      //             });
      //           }
      //         })
      //         .catch(failResponse => {});
      //     }
      //   }
      // });

      // if (
      //   this.loginForm.username == "cdy" &&
      //   this.loginForm.password == "123"
      // ) {
      //   this.$message({
      //     message: "登录成功，正在跳转。。。。",
      //     type: "success"
      //   });
      // }
      this.$axios
        .post("/login", {
          username: this.loginForm.username, //cdy
          password: this.loginForm.password //1234
        })
        .then(res => {
          if (res.data === 1) {
            // console.log(res.dat
            this.$message({
              message: "登录成功，正在跳转。。。。",
              type: "success"
            });
          }
        })
        .catch(failResponse => {});
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
      console.log(this.identifyCode);
    }
  }
};
</script>


<style>
#poster {
  background: url("../assets/8.jpg");
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
body {
  margin: 0px;
  padding: 0;
}

.code {
  width: 112px;
  height: 35px;
  border: 1px solid #ccc;
  float: right;
  border-radius: 2px;
}
.validate-code {
  width: 136px;
  float: left;
}

.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  position: relative;
  margin: 90px auto;
  width: 350px;
  height: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>



