import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import login from '@/components/Login'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base:__dirname,
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }


  ]
})



